
import {
  IonContent,
  IonItem,
  IonList,
  IonMenu,
  IonRouterOutlet,
  IonTitle,
  IonToolbar,
} from "@ionic/vue";
import { defineComponent} from 'vue';
export default defineComponent({
  name: 'SAPComponent',
  components: {
    IonContent,
    IonItem,
    IonList,
    IonMenu,
    IonRouterOutlet,
    IonTitle,
    IonToolbar,
  },
  data() {
    var list:any;
    fetch("article/sap.json").then(json => {list = json})
    return {
      sapArticleList: list,
    };
  },
});

